.btn {
  box-shadow: none !important;
  border: none!important;
  max-height: 60px!important;
  margin: auto auto;
}
.btn:disabled {
  background-color: var(--gray-300) !important;
  border-color: var(--gray-300) !important;
  a {
    color: var(--white)!important;
  }
}

.navbar * {
  font-family: 'Shadows Into Light', cursive !important;
  color: var(--primary) !important;
}

.selected-route {
  color: var(--gray-200)!important;
}

.prj {
  width: 170px!important;
  position: relative;
  &:not(.prj-selected):hover {
    color: var(--primary)!important;
  }
  &.prj-selected {
    color: var(--primary)!important;
    font-weight: bold!important;
  }
  &.prj-selected::after {
    position: absolute!important;
    content: ''!important;
    left: 0!important;
    right: 0!important;
    height: 4px;
    top: 35px;
    width: 170px;
    border-radius: 4px 4px 0 0;
    background-color: var(--primary)!important;
  }
}

div.repocard-container {

  >:last-child {
    margin-right: auto!important;
  }
  .repocard {
    max-width: 600px!important;
  }
}

.customCards {
  place-items: center;
  max-height: 400px;
  min-height: 400px;
  min-width: 300px;
  max-width: 300px;
  transform-style: preserve-3d;
  margin-bottom: 20px;
  perspective: 1000px;
  position: relative;
  .cardLabel label {
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    transition: transform 0.95s ease;
    transform-style: preserve-3d;
    .mainCard {
      .repo-hidden {
        position: absolute!important;
        height: 197.18px!important;
        background-color: #000;
        opacity: 0.9;
        &:hover {
          display: flex!important;
          justify-content: center!important;
          align-items: center!important;
          svg:hover {
            fill: #fff!important;
            transition: 0.01s!important;
          }
        }
      }
      .card-img-top {
        &:hover {
          +.repo-hidden {
            display: flex!important;
            justify-content: center!important;
            align-items: center!important;
          }
        }
      }
    }
    .card:not(.mainCard) {
      cursor: pointer;
    }
  .card-img-top {
    max-height: 197.18px;
    min-height: 197.18px;
  }
  }
  .card {
    position: absolute!important;
    transition: 0.2s;
    box-shadow: 0 0 5px -2px rgba(#000, 1);
    border: 1px solid #000;
    max-width: 300px;
    min-width: 300px;
    
    .card-title > a {
      color: var(--primary)!important;
    }
    > div > p  {
      max-height: 90px;
      display: -webkit-box;
      // max-height: 8rem;
      overflow: hidden;
      text-overflow: ellipsis;
      -webkit-line-clamp: 3;
      -webkit-box-orient: vertical;
    }
  }

  .mainCard:hover, > .mainCard:focus-within {
    position: absolute;
    z-index: 50000000;
    > div >p {
      max-height: fit-content;
      height: fit-content;
      -webkit-line-clamp: 10000;
    }
    transform: translateY(-1rem);
    position: relative;
    z-index: 2;

    ~ .card {
      transform: translateX(7.5rem);
    }
  }
}

button.next, button.prev {
  // border-radius: 0 5px 5px 0!important;
  border-top: 2px solid var(--primary)!important;
  border-left: 2px solid var(--gray-blue)!important;
  border-bottom: 2px solid var(--primary)!important;
  border-right: 2px solid var(--primary)!important;
}

button.next {
  border-radius: 0 5px 5px 0!important;
}
button.prev {
  border-radius: 5px 0 0 5px!important;
}

